import Head from '../components/head'
import axios from 'axios'
import GoingChart from '../components/going-chart'
import DiffTable from '../components/diff-table';
import MonthChart from '../components/month-chart';
import Menu from '../components/menu';
import Select from 'react-select';


export default class National extends React.Component {
  topYear = [
    {value: 2014, label: '2014'},
    {value: 2015, label: '2015'},
    {value: 2016, label: '2016'}
  ]

  state = {
    selectedYear: 2016
  }

  constructor(props) {
    super(props);
    this.handleYearChange = this.handleYearChange.bind(this);
  }

  static async getInitialProps() {
    const response = await axios.get('http://localhost:3000/BM/national/2016/?countries=Belgium,France');
    return {
      data: response.data,
      selectedYear: 2016
    }

  }

  handleYearChange = (selectedYear) => {
    // console.log();
    this.setState({ selectedYear: selectedYear['value'] });
  }
  
  
  handleSubmit(event) {
    event.preventDefault();
    console.dir(this.refs.year.value);
  }

  render() {
    const { selectedYear } = this.state;
    return (
      <div className="row">
        <div className="col-md-2">
          <div className="position-fixed" >
            <Menu>
              <form onSubmit={this.handleSubmit.bind(this)}>
                <h1>Menu</h1>
                <div className="form-group row">
                  <div className="col-md-10">
                    <Select value={selectedYear} onChange={this.handleYearChange} options={this.topYear}/>
                  </div>
                </div>
                <button type="submit" class="btn btn-primary">Update</button>
              </form>
            </Menu>
          </div>
        </div>
        <div className="col-md-10" style={{ borderLeft: '1px #e9ecef solid' }}>
          <Head title="National" />
          <h1>National</h1>
          <GoingChart evolution={this.props.data['Evolution']} year={this.props.selectedYear} />

          <DiffTable evolution={this.props.data['Evolution']} year={this.props.selectedYear} var='Ingoing' />
          <DiffTable evolution={this.props.data['Evolution']} year={this.props.selectedYear} var='Outgoing' />

          <div className="row">
            <div className="col-md-6">
              <MonthChart evolution={this.props.data['Monthly']} var='Ingoing' />
            </div>
            <div className="col-md-6">
              <MonthChart evolution={this.props.data['Monthly']} var='Outgoing' />
            </div>
          </div>
        </div>
      </div>
    )
  }

}